<template>
  <div>
    <p class="text-4xl text-MyColor-H3 font-bold pt-4">內建函式</p>
    <p class="text-3xl text-MyColor-H5">VB</p>
    <br />
    <ol class="text-justify list-decimal list-inside Indent">
      <li>沒啥重點看課本就對我是指VB部分</li>
      <li>需要注意的就是Cint和Int還有Fix</li>
      <li>Cint是4捨6入5取偶</li>
      <li>4.5 => 4</li>
      <li>3.5 => 4</li>
      <li>-4.5 => -4</li>
      <li>-3.5 => -4</li>
      <li>Int是回傳小於等於n的整數</li>
      <li>4.5 => 4</li>
      <li>3.5 => 3</li>
      <li>-4.5 => -5</li>
      <li>-3.5 => -4</li>
      <li>Fix是無條件捨去</li>
      <li>4.5 => 4</li>
      <li>3.5 => 3</li>
      <li>-4.5 => -4</li>
      <li>-3.5 => -3</li>
    </ol>
    <br />
    <a
      class=" text-indigo-400 text-2xl hover:text-purple-600"
      href="https://blog.csdn.net/u014451076/article/details/41396615"
      target="_blank"
      rel="noreferrer noopener"
      >點擊可以看更多VB函式</a
    >
    <br />
    <p class="text-3xl text-MyColor-H5">PyThon</p>
    <table class="mx-auto my-2 w-4/5">
      <thead>
        <tr>
          <th class="w-1/2 px-4 py-2">項目</th>
          <th class="w-1/4 px-4 py-2">說明</th>
          <th class="w-1/4 px-4 py-2">範例</th>
          <th class="w-1/4 px-4 py-2">結果</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="border px-4 py-2">str(n)</td>
          <td class="border px-4 py-2">n轉字串</td>
          <td class="border px-4 py-2">str(51)</td>
          <td class="border px-4 py-2">"51"</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">int(n)</td>
          <td class="border px-4 py-2">n轉數字</td>
          <td class="border px-4 py-2">int(False)</td>
          <td class="border px-4 py-2">0</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">int(n)</td>
          <td class="border px-4 py-2">n轉數字</td>
          <td class="border px-4 py-2">int(True)</td>
          <td class="border px-4 py-2">1</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">bool(n)</td>
          <td class="border px-4 py-2">n轉bool</td>
          <td class="border px-4 py-2">bool("")</td>
          <td class="border px-4 py-2">False</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">bool(n)</td>
          <td class="border px-4 py-2">n轉bool</td>
          <td class="border px-4 py-2">bool(" ")</td>
          <td class="border px-4 py-2">True</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">bool(n)</td>
          <td class="border px-4 py-2">n轉bool</td>
          <td class="border px-4 py-2">bool("A")</td>
          <td class="border px-4 py-2">True</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">bool(n)</td>
          <td class="border px-4 py-2">n轉bool</td>
          <td class="border px-4 py-2">bool(-1)</td>
          <td class="border px-4 py-2">True</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">bool(n)</td>
          <td class="border px-4 py-2">n轉bool</td>
          <td class="border px-4 py-2">bool(0)</td>
          <td class="border px-4 py-2">False</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">bool(n)</td>
          <td class="border px-4 py-2">n轉bool</td>
          <td class="border px-4 py-2">bool(1)</td>
          <td class="border px-4 py-2">True</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">range(初始值, 終止值, 跳值)</td>
          <td class="border px-4 py-2">
            回傳從初始值到終止值不含終止值，以跳值遞增的List
          </td>
          <td class="border px-4 py-2">range(2)</td>
          <td class="border px-4 py-2">[0, 1]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">range(初始值, 終止值, 跳值)</td>
          <td class="border px-4 py-2">
            1到1則是空的List假設是迴圈則不會跑
          </td>
          <td class="border px-4 py-2">range(1, 1)</td>
          <td class="border px-4 py-2">[]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">range(初始值, 終止值, 跳值)</td>
          <td class="border px-4 py-2">
            10到3則也是的List假設是迴圈則不會跑
          </td>
          <td class="border px-4 py-2">range(10, 3)</td>
          <td class="border px-4 py-2">[]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">range(初始值, 終止值, 跳值)</td>
          <td class="border px-4 py-2">
            7到3可是是用-1的方式則會跑
          </td>
          <td class="border px-4 py-2">range(7, 3, -1)</td>
          <td class="border px-4 py-2">[7, 6, 5, 4]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">range(初始值, 終止值, 跳值)</td>
          <td class="border px-4 py-2">
            0到10用+2的方式跑
          </td>
          <td class="border px-4 py-2">range(0, 10, 2)</td>
          <td class="border px-4 py-2">[0, 2, 4, 6, 8]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">迭代物件[初始值: 終止值: 跳值]</td>
          <td class="border px-4 py-2">
            同Range原理
          </td>
          <td class="border px-4 py-2">"abcdefg"[1:3]</td>
          <td class="border px-4 py-2">"bcd"</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">迭代物件[初始值: 終止值: 跳值]</td>
          <td class="border px-4 py-2">
            同Range原理
          </td>
          <td class="border px-4 py-2">"abcdefg"[-2:]</td>
          <td class="border px-4 py-2">"fg"</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">迭代物件[初始值: 終止值: 跳值]</td>
          <td class="border px-4 py-2">
            同Range原理
          </td>
          <td class="border px-4 py-2">[1, 2, 3, 4][::-1]</td>
          <td class="border px-4 py-2">[4, 3, 2, 1]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">迭代物件[初始值: 終止值: 跳值]</td>
          <td class="border px-4 py-2">
            同Range原理
          </td>
          <td class="border px-4 py-2">[1, 2, 3, 4, 5][1:4:2]</td>
          <td class="border px-4 py-2">[2, 4]</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">len(n)</td>
          <td class="border px-4 py-2">
            求n迭代物件長度
          </td>
          <td class="border px-4 py-2">len([2, 3])</td>
          <td class="border px-4 py-2">2</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">len(n)</td>
          <td class="border px-4 py-2">
            求n迭代物件長度
          </td>
          <td class="border px-4 py-2">len("abc")</td>
          <td class="border px-4 py-2">3</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">sum(n)</td>
          <td class="border px-4 py-2">
            求n迭代物件加總(迭代物件裡面的元素必須為數字不行是字串)
          </td>
          <td class="border px-4 py-2">sum([1, 2, 3])</td>
          <td class="border px-4 py-2">6</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">max(n)</td>
          <td class="border px-4 py-2">
            求n迭代物件最大
          </td>
          <td class="border px-4 py-2">max([1, 2, 3])</td>
          <td class="border px-4 py-2">3</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">max(n)</td>
          <td class="border px-4 py-2">
            C的Ascii碼最大(67)，A是65
          </td>
          <td class="border px-4 py-2">max("ABC")</td>
          <td class="border px-4 py-2">C</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">min(n)</td>
          <td class="border px-4 py-2">
            C的Ascii碼最大(67)，A是65
          </td>
          <td class="border px-4 py-2">min("ABC")</td>
          <td class="border px-4 py-2">A</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">chr(n)</td>
          <td class="border px-4 py-2">
            代入數字n得到數字n所對應的Ascii結果
          </td>
          <td class="border px-4 py-2">chr(65)</td>
          <td class="border px-4 py-2">A</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">ord(n)</td>
          <td class="border px-4 py-2">
            代入字母n得到對應的Ascii數字
          </td>
          <td class="border px-4 py-2">ord("A")</td>
          <td class="border px-4 py-2">65</td>
        </tr>
      </tbody>
    </table>
    <br />
    <br />
  </div>
</template>
<script>
export default {
  name: "IntrinsicsFunction"
};
</script>
